  constructor(public dialog: MatDialog) {
    this.asyncTabs = Observable.create((observer: Observer<ExampleTab[]>) => {
      setTimeout(() => {
        observer.next([
          { label: 'First', content: '<b>Content 1<b>' },
          { label: 'Second', content: 'Content 2' },
          { label: 'Third', content: 'Content 3' },
        ]);
      }, 1000);
    });
  }

  ngOnInit() {
    ymaps
      .ready()
      .then(() => {
        let lon = 41.311081;
        let lat = 69.240562;
        let iZoom = 12;
        let self = this;

        this.yandex_map = new ymaps.Map('map', {
          center: [lon, lat],
          zoom: iZoom,
        });

        var placemark = new ymaps.Placemark(
          [lon, lat],
          {},
          {
            preset: 'islands#circleDotIcon',
            iconColor: '#ff0000',
          }
        );

        this.yandex_map.geoObjects.add(placemark);
        this.yandex_map.events.add('boundschange', function() {
          self.updateYandexMap();
        });
      })
      .catch(console.error);
  }

  showHome(home_id: String) {
    console.log(home_id);
    this.dialog.open(HomeDetailsDialogComponent, {
      data: this.homeDetails,
      panelClass: 'home-details-dialog',
      autoFocus: false,
    });
  }

  updateYandexMap() {
    console.log(this.yandex_map.getBounds());
  }
