import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {
  userInfo = {
    name: 'Steve Jobs',
    type: 'Individual',
    phone: '(+998 99) 828-45-18',
    address: 'Street, City, Country',
  };

  userInfoForm: FormGroup;
  showUserNameEditForm = false;
  showUserInfoEditForm = false;

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.userInfoForm = this.formBuilder.group({
      name: [this.userInfo.name, Validators.required],
      type: [this.userInfo.type, Validators.required],
      phone: [this.userInfo.phone, Validators.required],
      address: [this.userInfo.address, Validators.required],
    });
  }

  onProfilePhotoSelected(photo) {
    console.log(photo);
  }

  showProfilePhoto() {
    const sa = swal.mixin();

    sa({
      title: '',
      html:
        '<img src="https://79chrrj514-flywheel.netdna-ssl.com/wp-content/uploads/2015/03/stevejobs1.jpg" alt="Profile photo" style="max-height: 90vh; max-width: 100%;"/>',
      showConfirmButton: false,
    });
  }

  getUserTypes(): Array<String> {
    return ['Individual', 'Entity'];
  }

  onUserRequestEditName(): void {
    this.showUserNameEditForm = true;
  }

  onUserSaveEditName(): void {
    const sweety = swal.mixin({
      toast: true,
      position: 'bottom',
      timer: 3000,
      showConfirmButton: false,
    });

    if (
      !this.userInfoForm.get('name').valid ||
      !this.userInfoForm.get('type').valid
    ) {
      sweety({
        type: 'error',
        title: 'Fill all fields',
      });
    } else {
      this.userInfo.name = this.userInfoForm.get('name').value;
      this.userInfo.type = this.userInfoForm.get('type').value;
      sweety({
        type: 'success',
        title: 'All done',
      });
      this.showUserNameEditForm = false;
    }
  }

  onUserRequestEditInfo(): void {
    this.showUserInfoEditForm = true;
    console.log("onedit");
  }

  onUserSaveEditInfo(): void {
    const sweety = swal.mixin({
      toast: true,
      position: 'bottom',
      timer: 3000,
      showConfirmButton: false,
    });

    if (
      !this.userInfoForm.get('phone').valid ||
      !this.userInfoForm.get('address').valid
    ) {
      sweety({
        type: 'error',
        title: 'Fill all fields',
      });
    } else {
      this.userInfo.phone = this.userInfoForm.get('phone').value;
      this.userInfo.address = this.userInfoForm.get('address').value;
      sweety({
        type: 'success',
        title: 'All done',
      });
      this.showUserInfoEditForm = false;
      console.log("onsave");
    }
  }
}
