  optionsHomeTitle = {
    minLength: 8,
    maxLength: 32,
    errorMessage: 'error',
  };

  optionsHomePrice = {
    minValue: 50,
    maxValue: 999999999,
    errorMessage: 'error',
  };

  optionsHomeYear = {
    minYear: 1970,
    maxYear: 2019,
    errorMessageBuilt: 'error',
    errorMessageReconstruction: 'error',
  };

  optionsBuildingLotSize = {
    minSize: 1,
    maxSize: 999,
    errorMessage: 'error',
  };

  optionsBuildingRooms = {
    minRooms: 1,
    maxRooms: 200,
    errorMessage: 'error',
  };

  buildingImages = [];
  buildingImagesPreview = [];
  buildingImagesDropZoneActive = false;

  buildingVideos = [];
  buildingVideosPreview = [];
  buildingVideosDropZoneActive = false;

  map: OlMap;
  source: OlXYZ;
  layer: OlTileLayer;
  view: OlView;

  constructor(private _formBuilder: FormBuilder) {}

  ngOnInit() {
    this.groupHomeDetails = this._formBuilder.group({
      homeTitle: [
        null,
        [
          Validators.required,
          Validators.minLength(this.optionsHomeTitle.minLength),
          Validators.maxLength(this.optionsHomeTitle.maxLength),
        ],
      ],
      personType: [null, Validators.required],
      moneyType: [null, Validators.required],
      homePrice: [
        null,
        [
          Validators.required,
          Validators.min(this.optionsHomePrice.minValue),
          Validators.max(this.optionsHomePrice.maxValue),
        ],
      ],
      action: [null, Validators.required],
      homeBuiltYear: [
        null,
        [
          Validators.min(this.optionsHomeYear.minYear),
          Validators.max(this.optionsHomeYear.maxYear),
        ],
      ],
      homeReconstructionYear: [
        null,
        [
          Validators.min(this.optionsHomeYear.minYear),
          Validators.max(this.optionsHomeYear.maxYear),
        ],
      ],
      buildingGroup: [null, Validators.required],
      buildingType: [null, Validators.required],
      homeRegion: [null, Validators.required],
      homeDistrict: [null, Validators.required],
      homeSubway: [null],
      homeZip: [null],
      homeAddress: [null],
      buildingImages: [null],
      buildingBasement: [null, Validators.required],
      buildingArchitecture: [null, Validators.required],
      buildingAppliances: [null],
      buildingIndoorFeatures: [null],
      buildingOutdoorAmenities: [null],
      buildingCoolingSystem: [null],
      buildingHeatingSystem: [null],
      buildingHeatingMethod: [null],
      buildingFloorCover: [null],
      buildingRoofType: [null],
      buildingSurrounding: [null],
      buildingSizeOption: [null, Validators.required],
      buildingLotSize: [
        null,
        [
          Validators.required,
          Validators.min(this.optionsBuildingLotSize.minSize),
          Validators.max(this.optionsBuildingLotSize.maxSize),
        ],
      ],
      phoneNumber: [
        null,
        [Validators.required, Validators.pattern('^[0-9]{10,12}$')],
      ],
      comment: [null, Validators.required],
      buildingRooms: [
        null,
        [
          Validators.min(this.optionsBuildingRooms.minRooms),
          Validators.max(this.optionsBuildingRooms.maxRooms),
        ],
      ],
    });

    this.onChanges();
  }

  ngAfterViewInit() {
    this.createMap();
  }

  onChanges() {
    this.groupHomeDetails.get('homePrice').disable();
    this.groupHomeDetails.get('moneyType').valueChanges.subscribe(selected => {
      if (!selected) {
        this.groupHomeDetails.get('homePrice').disable();
      } else {
        this.groupHomeDetails.get('homePrice').enable();
      }

      this.groupHomeDetails.get('homePrice').reset();
    });

    this.groupHomeDetails.get('buildingType').disable();
    this.groupHomeDetails
      .get('buildingGroup')
      .valueChanges.subscribe(selected => {
        if (!selected) {
          this.groupHomeDetails.get('buildingType').disable();
        } else {
          this.groupHomeDetails.get('buildingType').enable();
        }

        this.groupHomeDetails.get('buildingType').reset();
      });

    this.groupHomeDetails.get('homeDistrict').disable();
    this.groupHomeDetails.get('homeRegion').valueChanges.subscribe(selected => {
      if (!selected) {
        this.groupHomeDetails.get('homeDistrict').disable();
      } else {
        this.groupHomeDetails.get('homeDistrict').enable();
      }

      this.groupHomeDetails.get('homeDistrict').reset();
    });

    this.groupHomeDetails.get('homeSubway').disable();
    this.groupHomeDetails
      .get('homeDistrict')
      .valueChanges.subscribe(selected => {
        if (!selected) {
          this.groupHomeDetails.get('homeSubway').disable();
        } else {
          this.groupHomeDetails.get('homeSubway').enable();
        }

        this.groupHomeDetails.get('homeSubway').reset();
      });

    this.groupHomeDetails.get('buildingLotSize').disable();
    this.groupHomeDetails
      .get('buildingSizeOption')
      .valueChanges.subscribe(selected => {
        if (!selected) {
          this.groupHomeDetails.get('buildingLotSize').disable();
        } else {
          this.groupHomeDetails.get('buildingLotSize').enable();
        }

        this.groupHomeDetails.get('buildingLotSize').reset();
      });
  }

  createMap() {
    this.source = new OlXYZ({
      url: 'http://tile.osm.org/{z}/{x}/{y}.png',
    });

    this.layer = new OlTileLayer({
      source: this.source,
    });

    this.view = new OlView({
      center: fromLonLat([6.661594, 50.433237]),
      zoom: 3,
    });

    this.map = new OlMap({
      target: 'map',
      layers: [this.layer],
      view: this.view,
    });
  }

